<template>
    <div class="public_content">
        <div class="public_title">
          <span>小区录入</span>
           <el-button class="fr" icon="el-icon-back" size="small" @click='$router.go(-1)'>返回</el-button>
        </div>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">  
             <el-form-item label="小区名称" prop="community">
                <el-input v-model.trim="ruleForm.community" style="width: 320px"></el-input>
            </el-form-item>


 
                
          <el-form-item label="小区位置" prop="estate" class="inlineBlock">
            <v-address class="inlineBlock" :pregion='ruleForm.region' :pestate='ruleForm.estate' @emitChange='emitChange'/>
          </el-form-item>
      
          <el-form-item label-width="0" prop="address" class="inlineBlock">
            <el-input v-model.trim="ruleForm.address" placeholder="请输入详细地址" style="width: 270px"/> 

          </el-form-item>
  
          <div>
          <el-form-item label="经纬度" prop="lng" class="inlineBlock">
              <el-input style="width: 150px" v-model="ruleForm.lng"/>
          </el-form-item>
          <el-form-item label-width="0" prop="lat" class="inlineBlock">
              <el-input style="width: 150px" v-model="ruleForm.lat"/> 
              <i class="iconfont inlineBlock" style="color: #36A3F7;padding-left: 20px;cursor: pointer;" @click="$refs.location.visible = true">&#xe671;</i> 
              <span class="public_littleTip"><i class="iconfont">&#xe670;</i>点击图标进行标注</span>
              <location ref="location" @loactionChange='loactionChange'></location>
          </el-form-item>
        </div>

            <el-form-item label="物业费" prop="propertyfee">
              <el-input v-model="ruleForm.propertyfee" style="width: 200px">
                <span slot="suffix">元/㎡·月</span>
              </el-input>
            </el-form-item>
            <el-form-item label="小区缩略图" prop="thumb">
               <upload-img @onSuccess="changeLogo"></upload-img>
            </el-form-item>

            <el-form-item label="小区类型" prop="propertytype">
                <el-checkbox-group v-model="ruleForm.propertytype">
                    <el-checkbox v-for="item in housetype" :label="item.id" :key="item.id">
                        {{ item.paramname }}
                    </el-checkbox> 
                </el-checkbox-group>
            </el-form-item>

            <el-form-item label="建筑形态" prop="biulidingtype">
                <el-checkbox-group v-model="ruleForm.biulidingtype">
                    <el-checkbox v-for="item in decoration" :label="item.id" :key="item.id">
                        {{ item.paramname }}
                    </el-checkbox> 
                </el-checkbox-group>
            </el-form-item>

            <el-form-item label="特色标签" prop="feature">
                <el-checkbox-group v-model="ruleForm.feature">
                    <el-checkbox v-for="item in feature" :label="item.id" :key="item.id">
                        {{ item.paramname }}
                    </el-checkbox> 
                </el-checkbox-group>
            </el-form-item>

            <el-form-item label="小区介绍" style="margin-bottom: 120px;">
                <editor :value="ruleForm.content" 
                        @show="editors"     
                        @on-upload-success= "onEditorUploadComplete">
                </editor>
            </el-form-item> 

            <el-button type="primary" round @click="submitForm('ruleForm')" class="public_submit">确认提交</el-button>
         </el-form>
    </div>
</template>

<script>
import { addCommunity, getCommunityParam, getCommunity } from "@/api/house";
import uploadImg from "@/components/upload/uploadimg";
import vAddress from "@/components/address/index";
import location from "@/components/address/location";
import editor from "@/components/tinymce/editor";
import { store } from "@/store";
export default {
  components: {
    uploadImg,
    vAddress,
    editor,
    location
  },
  data() {
    return {
      flag: false,
      decoration: [],
      housetype: [],
      feature: [],
      ruleForm: {
        thumb: "",
        community: "",
        region: null,
        estate: null,
        address: "",
        biulidingtype: [],
        propertytype: [],
        feature: [],
        content: "",
        lng: "",
        lat: "",
        propertyfee: "" 
      },
      rules: {
        community: [
          { required: true, message: "请输入门店名称", trigger: "change" }
        ],
        lng: [{ required: true, message: "请选择经度" }],
        lat: [{ required: true, message: "请选择经度" }],
        estate: [{ required: true, message: "请选择区域和地段" }],
        address: [{ required: true, message: "请填写详细地址" }]
      }
    };
  },
  watch: {
    "ruleForm.community": function(val, oldVal) {
      getCommunity(this.ruleForm.community).then(res => {
        if (res.data.msg == "该小区不存在，可以添加！") {
          this.flag = true;
        } else {
          this.$message.error("此小区已被添加");
          this.flag = false;
        }
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    /* 从其他录入进来的 */ 
    if (to.params) {
      next(vm => {
        vm.ruleForm.community = to.params.search;
      });
    } else {
      next();
    }
  },
  /* 如果是从浏览器返回 */
  // beforeRouteLeave (to, from, next) {
  //   if (to.name != "addsecondhouse") {
  //     this.$store.commit("SET_SEARCH",{})
  //   }
  //   next()
  // },
  created() { 
    getCommunityParam().then(res => {
      this.feature = res.data.feature;
      this.decoration = res.data.decoration;
      this.housetype = res.data.housetype;
    });
  },
  methods: {
    submitForm(formName) {
      let _this = this;
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (!this.flag) {
            this.$message.error("此小区已被添加");
            return false;
          }
          addCommunity(this.ruleForm).then(res => {
            if (res.data.code == 1) {
              this.$message.success("添加成功");

              if (this.$route.params.type == 1) { 
                this.$router.push({
                  name: "addsecondhouse",
                  params: { search: this.ruleForm.community }
                });
              } else if (this.$route.params.type == 2) {
                 this.$router.push({
                  name: "addrent",
                  params: { search: this.ruleForm.community }
                });
              } else if (this.$route.params.type == 3) {
                 this.$router.push({
                  name: "addstore",
                  params: { search: this.ruleForm.community }
                });
              } else {
                this.$router.push({ name: "communitylists" });
              }
            } else {
              this.$message.error("添加失败");
            }
          });
        } else {
          return false;
        }
      });
    },

    changeLogo(src) { 
      this.ruleForm.thumb = src
    },
    /* 经纬度 */
    loactionChange(val) {
      this.ruleForm.lat = val.lat;
      this.ruleForm.lng = val.lng;
    },
    /* 地址接收 */
    emitChange(data) {
      this.ruleForm.region = data.region;
      this.ruleForm.estate = data.estate; 
    },
    editors(content) {
      // editor组件传过来的值赋给content
      this.ruleForm.content = content;
    },
    onEditorUploadComplete(json) {
      // 处理上传图片后返回数据，添加img标签到编辑框内
      // console.log(json);
      // console.log(json[0].data.filePath);
      this.content = this.content + "<img src=" + json[0].data.filePath + ">";
    }
  }
};
</script>

<style lang='scss' scoped>
</style>



